import { Component, OnInit } from '@angular/core';
import { MainService } from 'src/app/shared/services/main.service';

@Component({
  selector: 'app-widget-management',
  templateUrl: './widget-management.component.html',
  styleUrls: ['./widget-management.component.scss']
})
export class WidgetManagementComponent implements OnInit {
  showAction = null;
  edit = null;
  widgetNameDemo = 'Demo Assistant';
  deleteIndex;
  showDeleteDialog;
  showCode;

  code = `
  <script src="https://code.jquery.com/jquery-3.1.1.min.js"></script>
  
  <script src="https://code.highcharts.com/highcharts.js"></script>
  
  <script src="https://code.highcharts.com/modules/data.js"></script>
  
  <script src="https://code.highcharts.com/modules/series-label.js"></script>
  
  <script src="https://code.highcharts.com/modules/exporting.js"></script>
  
  <script src="https://code.highcharts.com/modules/export-data.js"></script>
  
  
  
  <!-- Additional files for the Highslide popup effect -->
  
  <script src="https://www.highcharts.com/media/com_demo/js/highslide-full.min.js"></script>
  
  <script src="https://www.highcharts.com/media/com_demo/js/highslide.config.js" charset="utf-8"></script>
  
  <link rel="stylesheet" type="text/css" href="https://www.highcharts.com/media/com_demo/css/highslide.css" />
  
  
  
  <div id="container" style="min-width: 310px; height: 400px; margin: 0 auto"></div>
  
  <script src="https://code.jquery.com/jquery-3.1.1.min.js"></script>
  
  <script src="https://code.highcharts.com/highcharts.js"></script>
  
  <script src="https://code.highcharts.com/modules/data.js"></script>
  
  <script src="https://code.highcharts.com/modules/series-label.js"></script>
  
  <script src="https://code.highcharts.com/modules/exporting.js"></script>`
  constructor(private mainService: MainService) { }

  widgetData = [];

  ngOnInit() {
    this.widgetData = this.mainService.getWidget();
  }

  detaleItem(flag, index?, event?) {
    if (flag === 'dialog'){
      this.deleteIndex = index;
      this.showDeleteDialog = true;
    } else if (event) {
      this.widgetData.splice(this.deleteIndex, 1);
      console.log('delete elem', index + 1);
      this.showDeleteDialog = false;
    } else if (!event) {
      this.showDeleteDialog = false;
    } 
  }
  dublicateWidget(item, index){
    this.widgetData.splice(index, 0, Object.assign({}, item));
  }
}
