import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MainComponent } from './main/main.component';
import { ContentManagementComponent } from './main/content-management/content-management.component';
import { WidgetManagementComponent } from './main/widget-management/widget-management.component';
import { ArticleComponent } from './main/content-management/article/article.component';
import { NewWidgetComponent } from './main/widget-management/new-widget/new-widget.component';
import { ContentComponent } from './main/widget-management/new-widget/content/content.component';
import { CustomizeComponent } from './main/widget-management/new-widget/customize/customize.component';
import { EmdedComponent } from './main/widget-management/new-widget/emded/emded.component';
import { RequestsComponent } from './main/widget-management/new-widget/requests/requests.component';
import { StatisticsComponent } from './main/widget-management/new-widget/statistics/statistics.component';

const routes: Routes = [
  {
    path: '',
    component: MainComponent,
    children: [
      { path: 'content-manage', component: ContentManagementComponent },
      { path: 'widget-manage', component: WidgetManagementComponent },
      { path: 'article/:id', component: ArticleComponent },
      { path: 'article', component: ArticleComponent },
      {
        path: 'new-widget',
        component: NewWidgetComponent,
        children: [
          { path: 'content/:id', component: ContentComponent },
          { path: 'customize/:id', component: CustomizeComponent },
          { path: 'emded/:id', component: EmdedComponent },
          { path: 'requests/:id', component: RequestsComponent },
          { path: 'statistics/:id', component: StatisticsComponent },
        ]
      },
      {
        path: 'new-widget',
        component: NewWidgetComponent,
        children: [
          { path: 'content', component: ContentComponent },
          { path: 'customize', component: CustomizeComponent },
          { path: 'emded', component: EmdedComponent },
          { path: 'requests', component: RequestsComponent },
          { path: 'statistics', component: StatisticsComponent },
        ]
      },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
