import { Component, OnInit } from '@angular/core';
import { MainService } from 'src/app/shared/services/main.service';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-new-widget',
  templateUrl: './new-widget.component.html',
  styleUrls: ['./new-widget.component.scss']
})
export class NewWidgetComponent implements OnInit {
  widgetName;
  data;
  dataToEdit
  id;
  constructor(private mainService: MainService, private router: Router, private activatedRoute: ActivatedRoute) {
    this.data = this.mainService.getWidget()
    let curentId = +this.router.url.split('/')[this.router.url.split('/').length - 1]
    this.activatedRoute.params.subscribe(params => {
    });
    if (!isNaN(curentId)) {
      this.id = curentId;
      for (const key in this.data) {
        if (+this.data[key].id === +this.id) {
          this.dataToEdit = this.data[key];
          this.widgetName = this.dataToEdit.name;
        }
      }
    }

  }

  ngOnInit() {

  }

  save() {
    if (!this.id) {
      this.mainService.addWidget(this.widgetName)
    } else {
      this.dataToEdit.name = this.widgetName;
      this.mainService.editWidget(this.dataToEdit)
    }
    this.router.navigateByUrl('/widget-manage');
  }
  cancel() {
    this.widgetName = null;
    this.dataToEdit = {};
    this.router.navigateByUrl('/widget-manage');
  }

  goToTab(url) {
    if (!isNaN(this.id)) {
      this.router.navigate([url + '/' + this.id]);
    } else {
      this.router.navigateByUrl(url);
    }
  }

}
